/* eslint-disable no-unused-vars */
import Data from "@/js/core/data";
const Optin_server = "https://api.${region}.500apps.com/mailsend/double-optin/subscribe/";
const Optin_server_get = "https://api.${region}.500apps.com/mailsend/v1/double-optin/";
const Contact_server = "https://api.${region}.500apps.com/mailsend/v1/double-optin/status/";

class DoubleOptin extends Data {
  constructor() {
    super(Optin_server);
    this.disablePCors();
  }

  // Get Contact Data
  async getContactData(domain_id, contact_id) {
    let region = this.getRegion();
    let url = `${Contact_server}${domain_id}/${contact_id}`;
    this.url = eval("`" + url + "`");
    return await super.get();
  }

  // Get Optin Data
  async getOptinData(domain_id, project_id) {
    let region = this.getRegion();
    let url = `${Optin_server_get}${domain_id}/${project_id}`;
    this.url = eval("`" + url + "`");

    return await super.get();
  }

  // Optin Status Update Call
  async optinCall(contact_id, domain_id, optin_type, project_id) {
    let region = this.getRegion();
    let url = `${Optin_server}${domain_id}/${contact_id}/${project_id}?opt_in_type=${optin_type}`;
    this.url = eval("`" + url + "`");
    return await super.get();
  }
}
export default new DoubleOptin();
