<template>
  <b-container>
    <div v-if="optin_show && !contact_data.status">
      <b-row>
        <b-col md="9" class="mx-auto mt-4">
          <b-row>
            <b-col md="12">
              <b-card body-class="p-6">
                <div class="d-flex align-items-center pb-1">
                  <b-img :src="optin_data[0].logo_url" alt="logo" class="rounded-circle" width="40" height="40" v-if="optin_data[0].logo_url" />
                  <h2 class="mb-0 ml-3 brand-heading">{{ optin_data[0].company_name }}</h2>
                </div>

                <div class="py-4">
                  <h1 class="mb-0 display-4">Marketing Permissions</h1>
                </div>

                <p class="mb-0 font-size-lg">
                  {{ optin_data[0].company_name }} will use the information you provide on this form to be in touch with you and to provide updates and marketing. Please let us know all the ways you
                  would like to hear me from us:
                </p>
                <br />
                <b-form @submit.prevent="doubleOptinCall()" class="text-center">
                  <b-form-group id="input-group-4" v-slot="{ ariaDescribedby }">
                    <b-form-checkbox-group id="checkbox-group-2" v-model="form.optin_type" :aria-describedby="ariaDescribedby" name="flavour-2">
                      <b-form-checkbox value="email">Email</b-form-checkbox>
                      <b-form-checkbox value="direct email">Direct Mail</b-form-checkbox>
                    </b-form-checkbox-group>
                  </b-form-group>
                </b-form>
                <p class="mb-0 font-size-lg pb-4">
                  You can change your mind at any time by clicking on unsubscribe link in the footer of any email you receive from us, or by contacting us at <b>{{ optin_data[0].company_email }}</b
                  >. we will treat your information with respect. For more information about our privacy policy please visit our website. By clicking below you agree that we may process your
                  information in accordance with these terms.
                </p>
                <hr />
                <b-row class="pt-4" v-if="optin_data[0].is_gdpr_enabled">
                  <b-col md="2"> <b-img src="https://ap1-dev-infinity-user-data.s3.amazonaws.com/410/mailsend/gdpr.png" alt="logo" width="100" height="100" class="rounded" /> </b-col>
                  <b-col md="10" class="my-auto ">
                    <div class="text-muted">
                      We use Mailsend as our email marketing platform. By submitting this page you acknowledge that the information you provide will be transfered to Mailsend for processing in
                      accordance with <b-link class="font-weight-bold" :href="optin_data[0].privacy_policy">Privacy Policy</b-link> and
                      <b-link class="font-weight-bold" :href="optin_data[0].terms"> Terms </b-link>.
                    </div>
                  </b-col>
                </b-row>
                <b-row>
                  <b-col md="12" class="mt-2 text-right">
                    <div class="py-10 ml-5">
                      <b-button variant="primary" class="px-5" type="submit" @click="doubleOptinCall()">Subscription</b-button>
                    </div>
                  </b-col>
                </b-row>
              </b-card>
            </b-col>
          </b-row>
        </b-col>
      </b-row>
    </div>
    <div v-else>
      <div class="confirmation">
        <b-row class="m-0 pt-7">
          <b-col cols="7" class="text-center card mx-auto border-0 text-center py-6 rounded">
            <div><span class="display-1 fe fe-check-circle font-weight-normal text-success pulse"></span></div>
            <h4 class="pt-3 mb-3" v-if="!contact_data.status">Subscribed sucessfully!</h4>
            <h4 class="pt-3 mb-3" v-if="contact_data.status">Already Subscribed!</h4>
          </b-col>
        </b-row>
      </div>
    </div>
  </b-container>
</template>
<script>
  import DoubleOptin from "@/js/mailsend/double-optin.js";

  export default {
    data() {
      return {
        page_name: this.$route.params.page,
        optin_show: true,
        optin_data: [],
        contact_data: {},
        form: {
          optin_type: [],
        },
      };
    },
    mounted: async function() {
      this.optin_show = true;
      this.contact_data = await DoubleOptin.getContactData(window.domain, this.$route.params.contact_id);
      this.getOptinData();
    },
    methods: {
      //Get Optin Data
      async getOptinData() {
        this.optin_data = await DoubleOptin.getOptinData(window.domain, window.user);
      },

      // Optin Status Update Call
      async doubleOptinCall() {
        this.form.optin_type = this.form.optin_type.toString().replace(",", ", ");
        let response = await DoubleOptin.optinCall(this.$route.params.contact_id, window.domain, this.form.optin_type, window.user);
        if (response.status == "Success") this.optin_show = false;
      },
    },
  };
</script>
